import config from './../../config/config'
export default {
  props: ['adResource', 'pwsId', 'baseInfo'],
  data: function () {
    return {
      advertPlayRecordIds: [],
      indicators: true,
      isFinish: false,
      intervalId: 0,
      intervalTime: 0
    }
  },
  computed: {
    interval: function () {
      var intervals = []
      for (let i = this.adResource.length - 1; i >= 0; i--) {
        intervals.push(parseInt(this.adResource[i].ADVERT_PLAY_TIME) * 1000)
      }
      return intervals
    },
    adIds: function () {
      var adIds = []
      for (let i = this.adResource.length - 1; i >= 0; i--) {
        adIds.push(this.adResource[i].ADVERT_ID)
      }
      return adIds
    },
    remainTime: function () {
      if (this.intervalTime <= 0 || this.isFinish) {
        // return ''
      }
      return this.intervalTime
    }
  },
  watch: {
    'interval': function (val, oldVal) {
      if (val && val[0]) {
        this.swiperInit()
      }
    }
  },
  methods: {
    adClick: function (ad) {
      var advertPlayRecordId = ''
      for (var i = this.advertPlayRecordIds.length - 1; i >= 0; i--) {
        if (this.advertPlayRecordIds[i].ADVERT_ID === ad.ADVERT_ID) {
          advertPlayRecordId = this.advertPlayRecordIds[i].ADVERT_PLAY_RECORD_ID
        }
      }
      // 广告点击事件
      this.$http.jsonp(config.jsonp + config.api.adClick + advertPlayRecordId)
      let url = ad.LINK_URL
      if (!url) return false
      if (url && url.indexOf('http://') !== 0) {
        url = 'http://' + url
      }
      window.open(url, '_blank')
    },
    viewAd: function (adId) {
      // 查看广告
      var ts = this
      this.$http.jsonp(config.jsonp + config.api.adPlayRecord + adId + '&pwsId=' + this.pwsId + '&macAddress=' + this.baseInfo.macAddress).then(function (response) {
        ts.advertPlayRecordIds.push({
          ADVERT_PLAY_RECORD_ID: response.data.resultList.ADVERT_PLAY_RECORD_ID,
          ADVERT_ID: adId
        })
        console.log(ts.advertPlayRecordIds)
      })
    },
    swiperInit: function () {
      var ts = this
      var timeoutManager = function (time, swiper, realIndex) {
        if (ts.isFinish) {
          swiper.unlockSwipes()
          swiper.unlockSwipeToPrev()
          return false
        }
        ts.viewAd(ts.adIds[realIndex])
        swiper.lockSwipes()
        swiper.lockSwipeToPrev()
        ts.intervalTime = time / 1000
        ts.intervalId = setInterval(function () {
          ts.intervalTime--
        }, 1000)
        setTimeout(function () {
          swiper.unlockSwipes()
          swiper.lockSwipeToPrev()
          if (swiper.activeIndex === ts.interval.length) {
            ts.isFinish = true
            swiper.unlockSwipeToPrev()
            ts.$dispatch('enable-online')
          }
          if (ts.intervalId) {
            clearInterval(ts.intervalId)
            ts.intervalId = 0
          }
        }, time)
      }
      Swiper('.swiper-container', {
        loop: true,
        pagination: '.swiper-pagination',
        prevButton: '.swiper-button-prev',
        nextButton: '.swiper-button-next',
        onSlideNextEnd: function (swiper) {
          var realIndex = 0
          if (swiper.activeIndex > ts.interval.length) {
            realIndex = 0
          } else {
            realIndex = swiper.activeIndex - 1
          }
          timeoutManager(ts.interval[realIndex], swiper, realIndex)
        }
      })
    }
  }
}



