import config from './config/config'
import AppHeader from './components/header/header.vue'
import AdCarousel from './components/ad-carousel/ad-carousel.vue'

export default {
  data: function () {
    return {
      adResource: {
        'retState': '0',
        'resultList': [],
        'retMessage': '查询成功'
      },
      pwsId: '31',
      sceneCode: '',
      deviceType: '01',
      onlineStatus: {
        disabled: true
      },
      baseInfo: {
        pwsConnectInfoId: null,
        macAddress: null,
        ip: null
      }
    }
  },
  components: {
    AdCarousel,
    AppHeader
  },
  events: {
    'enable-online': function (msg) {
      this.onlineStatus.disabled = false
    }
  },
  methods: {
    onlineNow: function () {
      var ts = this
      if (!this.onlineStatus.disabled) {
        this.postOnlineRecord()
        window.location.href = config.api.authOnekey + '?sceneCode=' + ts.sceneCode
      }
    },
    getPwsId: function () {
      return this.$http.jsonp(config.api.getPwsId)
    },
    getAdResource: function (pwsId) {
      // 获取广告资源
      return this.$http.jsonp(config.jsonp + config.api.getAdResource + this.pwsId).then(function (response) {
        if (response.data.retState === '0') {
          this.adResource.resultList = response.data.resultList
        } else {
          this.$emit('enable-online')
        }
      }, function () {
        this.$emit('enable-online')
      })
    },
    getSceneCode: function (pwsId) {
      // 获取pws对应的场景(ip端口可配置，不能写死)
      return this.$http.jsonp(config.jsonp + config.api.getSceneCode + 'deviceType=' + this.deviceType + '&deviceCode=' + this.pwsId)
    },
    getMacIP: function () {
      return this.$http.jsonp(config.api.getMacIP)
    },
    visitRecord: function () {
      var ts = this
      this.$http.jsonp(config.jsonp + config.api.visitRecord + this.pwsId + '&sceneCode=' + this.sceneCode + '&macAddress=' + this.baseInfo.macAddress).then(function (response) {
        ts.baseInfo.pwsConnectInfoId = response.data.pwsConnectInfoId
      })
    },
    postOnlineRecord: function () {
      this.$http.jsonp(config.jsonp + config.api.postOnlineRecord + this.baseInfo.pwsConnectInfoId)
    }
  },
  ready () {
    let ts = this
    this.getPwsId().then(function (response) {
      if (response.data.data && response.data.data.result) {
        ts.pwsId = response.data.data.result
        ts.getAdResource(ts.pwsId)
        ts.getSceneCode(ts.pwsId).then(function (response) {
          if (response.data.data.sceneCode) {
            ts.sceneCode = response.data.data.sceneCode
              // 通知基站有用户连接并记录用户信息
            ts.getMacIP().then(function (response) {
              ts.baseInfo.macAddress = encodeURI(response.data.data.mac)
              ts.baseInfo.ip = response.data.data.ip
              ts.visitRecord()
            })
          }
        })
      }
    }, function () {
      ts.getAdResource(ts.pwsId)
    })
  }
}



