import './vendors/swiper/swiper.min.js'
import Vue from 'vue'
import resource from 'vue-resource'
import touch from 'vue-touch'
import App from './App.vue'
/* eslint-disable no-new */
Vue.config.debug = true
Vue.use(resource)
Vue.use(touch)

new Vue({
  el: 'body',
  components: { App }
})



